					</div>
					<div class="">
						<div class="histor_multiple_box table " style="max-width:750px">
							<el-table  :data="tableData" border  >
								<el-table-column v-for="(tableHeaderitem,index) of tableHeader " :key="index" :prop="index.toString()" :label="tableHeaderitem" width='120'>
									<template slot-scope="scope">
										<el-input v-model="scope.row[index]" :disabled="isDisabledEditable" clearable @blur="historyCheck(scope.row[index],index)">
										</el-input>
									</template>
								</el-table-column>
								<el-table-column fixed="left" label="年份" width="70">
