				<button @click="toAddMotherFund">保存</button>
			</div>
		</div>
		<!--公司工商信息模块开始-->
		<div class="company_background">
			<div class="block_title flex_between">
				<div class="title_left flex_start">
					<div class="radio"> </div>
					<div class="block_name">
						合伙企业信息
					</div>
				</div>
				<div class=" flex_end">
					<div class="table-group-title-btn btn-blue-border" @click="downloadEquityInfo">
						<span class="iconfont icon-daoru"></span>导出股权穿透表
					</div>
					<div class="table-group-title-btn btn-blue" @click="downLoadCompanyInfo">
						<span class="iconfont icon-daoru"></span>导出企业信息
					</div>
				</div>
			</div>
			<company-detail-background v-bind:type="type"></company-detail-background>
