<script>
	import companyDetailBackground from '../../pages/companyBackground/companyDetailBackground.vue'
	import url from '../../config/url'
	import {getUserlistApi,downloadEquityInfoApi} from '../../config/api'
	// import api from "../../config/url"; //引用url.js
	import {monterFundSelectData,MonterFundDetail,editMonterFund,getParentFof,ShareholderGet,} from '../../config/apiSj'
    import {dialogConfirm,dialogConfirmHasbtn,isValidNumber,isValidDecimal} from '../../config/mUtils'
	export default {
		name: 'motherFundCompanyDetail',
		data() {
			return {
				isDisabledEditable: true,
				motherFundId:null,
				motherFundName:'',
				commitData:{
					is_hide:1,
					fund_end_date:['','',''],
				},
				allRecordState:[],//备案状态下拉
				allFundState:[],//基金状态
				allCollectState:[],//募集状态
				allSgCompanyManagerList:[],//所有管理公司列表
				allInvestor:[],//所有投资人列表
				creatorList:[],//创建人列表
				moneyTypeList:[
					{
						id:1,
						name:'人民币'		
					},
					{
						id:2,
						name:'美元'		
					},
				],//币种列表
				fundTypeList:[
					{
						id:1,
						name:'主体母基金'		
					},
					{
						id:0,
						name:'分支母基金'		
					},
				],//基金类型
				masterMotherFundList:[],
				isChangeList:[
					{
						id:0,
						name:'正常'		
					},
					{
						id:1,
						name:'待变更'		
					},
					
				],//工商状态
				//编辑数据
				copyData:{},
				type:3,//舆情固定参数2
				
				//表头数据
				tableHeader:[],
				//表格数据
				tableData: [{}],

				alertData:[],//弹窗数据//
				dialogVisible: false,
			}
		},
		created() {
			if(this.$route.query.id) {
				this.motherFundId = this.$route.query.id
				this.getDetailData();

				this.getAllUserList();
				this.toGetParentFof();
				this.getMonterFundSelectData();
				
			}
		},
		mounted() {
		},
		components: {
			companyDetailBackground
		},
		methods: {
			historyCheck(multiple,index){
				if(Number(multiple)!=multiple || multiple<0){
					dialogConfirm('info','历史倍数格式错误或小于0');
					this.tableData[0][index] = 0;
				}
				if(this.tableData[0][index]==''){
					this.tableData[0][index] = 0;
				}
			},
			//编辑非编辑切换
			changeEditStatus() {
				this.copyData = JSON.parse(JSON.stringify(this.commitData));
				this.isDisabledEditable = false;
				this.$nextTick(function () {
		        		this.$refs.input1.$el.querySelector('input').focus();
		        });
			},
			//是否隐藏切换
			changeHideStatus(e){
				if(!this.isDisabledEditable){
					if (e == 1) {
						this.commitData.is_hide = 1;
					} else{
						this.commitData.is_hide = 0;
					}
				}
			},
			//获取详情页面
			getDetailData(){
				MonterFundDetail({
					id:this.motherFundId
				}).then(res => {
					if (res.data.msgNo == 1) {
						let data  = res.data.payload.data;

						this.tableHeader = [];
						this.tableData = [{}],
						data.history.forEach((item,index) => {
							this.tableHeader.push(item.year.toString());
							this.tableData[0][index] = item.multiple;
						});

						
						this.motherFundName = data.name;
						this.commitData = data; 
						this.commitData.fund_end_date = this.commitData.fund_end_date.split('+');
						if(this.commitData.fund_end_date.length==1){
							this.commitData.fund_end_date[1] = '';
							this.commitData.fund_end_date[2] = '';
						}else if(this.commitData.fund_end_date.length==2){
							this.commitData.fund_end_date[2] = '';
						}

						// console.log(this.commitData.fund_end_date);
						if(!this.commitData.lp_user_front_id){
							this.commitData.lp_user_front_id = '';
						}
						if(!this.commitData.sg_company_manager_id){
							this.commitData.sg_company_manager_id = '';
						}
						if(this.commitData.is_master===1){
							this.commitData.p_fof_id = '';
						}else{
							if(this.commitData.p_fof_id===0){
								this.commitData.p_fof_id = '';
							}
						}

						
						//历史倍数
					}else{
						dialogConfirm('info', res.data.message);
					}
				})
			},
			//获取母基金下拉数据
			getMonterFundSelectData(){
				monterFundSelectData({
				}).then(res => {
					if (res.data.msgNo == 1) {
						let data  = res.data.payload;
						this.allCollectState = data.all_collect_state;
						this.allFundState = data.all_fund_state;
						this.allRecordState= data.all_record_state;
						this.allInvestor = data.all_investor;
						this.allSgCompanyManagerList = data.all_sg_company_manager_lst
					}
				})
			},
			// 获取数据创建人列表
			getAllUserList(){
				getUserlistApi({
					number: 10000,
					page: 1
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.creatorList = res.data.payload.data;
					}
				})
			},
			//获取所有的主体母基金
			toGetParentFof(){
				getParentFof().then(res => {
					if(res.data.msgNo == 1) {
						let data = res.data.payload.data;
						// console.log(res);
						this.masterMotherFundList = data;
					}
				})
			},
			//取消添加母基金
			cancleAddMotherFund(){
				this.commitData = JSON.parse(JSON.stringify(this.copyData));
				this.isDisabledEditable = true;
			},
			
			// 判断基金期限是否有效
			isValidFundTime(){
				if (this.commitData.fund_end_date[0]) {
					let is_valid_fund_expire1 = isValidNumber(this.commitData.fund_end_date[0]);
					let is_valid_fund_expire2 = isValidNumber(this.commitData.fund_end_date[1]);
					let is_valid_fund_expire3 = isValidNumber(this.commitData.fund_end_date[2]);
					if(this.commitData.fund_end_date[1]&&!is_valid_fund_expire2){
						dialogConfirm("info", '基金期限输入格式不对');
						return false
					}
					if(this.commitData.fund_end_date[2]&&!is_valid_fund_expire3){
						dialogConfirm("info", '基金期限输入格式不对');
						return false
					}
					if (is_valid_fund_expire1) {
						return true
					} else{
						dialogConfirm("info", '基金期限输入格式不对');
						return false
					}
				} else{
					dialogConfirm("info", '基金期限不能为空');
					return false
				}
			},
			
			//编辑母基金信息
			toAddMotherFund(){
				var commitData = JSON.parse(JSON.stringify(this.commitData));
				if(this.commitData.odi!=''){
					if(!isValidDecimal(this.commitData.odi)||parseFloat(this.commitData.odi) < 0){
						dialogConfirm('info', 'ODI额度应大于等于0');
						this.commitData.odi = '';
						return
					}
				}
				//验证合伙企业、基金简称、管理公司比填
				if(this.commitData.name&&this.commitData.short_name&&this.commitData.sg_company_manager_id){
					var is_valid_number = this.isValidFundTime();
					if (is_valid_number) {
						if(commitData.fund_end_date[1]==''&&commitData.fund_end_date[2]==''){
							commitData.fund_end_date = commitData.fund_end_date[0];
						}else if(commitData.fund_end_date[1]==''&&commitData.fund_end_date[2]!=''){
							dialogConfirm("info", '基金期限不能为空!');
							return;
						}else if(commitData.fund_end_date[1]!=''&&commitData.fund_end_date[2]==''){
							commitData.fund_end_date.splice(2,1);
							commitData.fund_end_date = commitData.fund_end_date.join('+');
						}else{
							commitData.fund_end_date = commitData.fund_end_date.join('+');
						}
					} else {
						return
					}
					this.creatorList.forEach((item,index) => {
						if(item.id==commitData.create_user_id){
							commitData.create_user_name = item.name;
						}
					});
					let params = {
						name:commitData.name,
						short_name:commitData.short_name,
						is_master:commitData.is_master,
						unit:commitData.unit,
						protocol_end_date:commitData.protocol_end_date,
						fund_end_date:commitData.fund_end_date,
						record_state:commitData.record_state,
						sg_company_manager_id:commitData.sg_company_manager_id,
						lp_user_front_id:commitData.lp_user_front_id,
						fund_date:commitData.fund_date,
						odi:commitData.odi,
						create_user_id:commitData.create_user_id,
						fund_state:commitData.fund_state,
						collect_state:commitData.collect_state,
						record_state_number:commitData.record_state_number,
						is_hide:commitData.is_hide,
						collect_state:commitData.collect_state,
						create_user_name:commitData.create_user_name,
						id:this.motherFundId,
						tianyancha_id:'',
						check_multiple:commitData.check_multiple
					}
					if(commitData.is_master===0){
						if(commitData.p_fof_id){
							params.p_fof_id = commitData.p_fof_id;
						}else{
							params.p_fof_id = 0;
						}
					}else{
						params.p_fof_id = 0;
					}

					
					let historyArr = [];
					this.tableHeader.forEach((element,index) => {
						let newObj = {
							year:element,
							multiple:this.tableData[0][index]?this.tableData[0][index]:'0',
						};
						historyArr.push(newObj);
					});
					if(historyArr.length){
						params.history = JSON.stringify(historyArr);
					}else{
						params.history = '';
					}
					editMonterFund(params).then(res => {
						if (res.data.msgNo == 1) {
							let data  = res.data.payload;
							dialogConfirm('success', res.data.message);
							this.isDisabledEditable = true;
							this.getDetailData();
						}else{
							dialogConfirm('info', res.data.message);
						}
					})
				}else{
					if (!this.commitData.name) {
						dialogConfirm("info", '合伙企业不能为空');
					}else if(!this.commitData.short_name){
						dialogConfirm("info", '基金简称不能为空');
					}else if(!this.commitData.sg_company_manager_id){
						dialogConfirm("info", '管理公司不能为空');
					}
				}

			},

			//导出企业信息
			downLoadCompanyInfo(){
				let key = window.localStorage.getItem("htcx_server_key");
				let href = url.sourceDataUrl + "/web/Company/CompanyInfoExcel?company_id=" + this.motherFundId + '&key=' + key + '&new_type=' + this.type;
				window.location.href = href;
			},

			//去基金运行
			toFundRunPage(){
				// console.log();
				// if (row.auth.fund_operate) {
					this.$router.push({
						name: "motherFundRun",
						query: {
							id: this.motherFundId,
							type: 1
						}
					})
				// } else{
				// 	dialogConfirm("info", '您无权限操作该条数据');
				// }
				
			},

			// 获取工商状态带变更数据
			getSatateList(){
				ShareholderGet({
					company_name:this.commitData.name,
				}).then(res => {
					if (res.data.msgNo == 1) {
						let data = res.data.payload;
						data.forEach((element,index) => {
							element.index = index+1;
						});
						this.dialogVisible = true;
						this.alertData = data;
					}else{
						dialogConfirm('info', res.data.message);
					}
				})
			},

			//下载股权穿透图
			downloadEquityInfo(){
				downloadEquityInfoApi({
					company_name: this.commitData.name
				}).then(res => {
					if(res.data.msgNo == 1) {
						window.location.href = res.data.payload.url;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},

			//计算出来对应的历史倍数年限
			toCalcYear(){
				this.tableHeader = [];
				this.tableData = [{}];
				if(this.commitData.fund_date&&this.commitData.protocol_end_date){
					let  fund_date = Date.parse(this.commitData.fund_date);//工商设置日期
					let  end_date = Date.parse(this.commitData.protocol_end_date);//基金到期日
					if(fund_date <= end_date){
						let fund_year = this.commitData.fund_date.split('-')[0];
						let end_year = this.commitData.protocol_end_date.split('-')[0];
						let number = parseInt(end_year) - parseInt(fund_year) ;
						for(let i = 0;i<=number;i++){
							this.tableHeader.push((parseInt(fund_year)+i).toString());
						}
					}else{
						this.commitData.fund_date = '';
						dialogConfirm('info','工商设立日期不能大于基金到期日');
					}
				}
				
				if(this.tableHeader.length){
					let tableDataObj = [{}];
					this.tableHeader.forEach((element,index) => {
						tableDataObj[0][index] = 0;
					});
					this.tableData=tableDataObj;
				}
				
			}
		},
	}
</script>

